body {
  margin: 0;
  padding: 0;
  /* font-family: sans-serif; */
}

.App {
    font-family: 'Nunito Sans', sans-serif;
    
}



.active {
    font-weight: 900;
}

.navBarLogo{
    font-weight: 400;
    font-size: 34px;
    color: rgb(66, 66, 66);
    font-family: 'Poppins', sans-serif;
    /* text-decoration: none; */
}

.navBarLogo:hover{
    color: #141414;
}

.nav-link{
    font-weight: 600;
    font-size: 16px;
    padding: 25px;
    margin: 0px 5px;
    /* border: none;  */
    border: transparent solid 3px;
    -webkit-transition: color .3s, border .3s; 
    -webkit-transition: linear color .3s, ease-in border .3s; 
    transition: linear color .3s, ease-in border .3s;
}
.nav-link:hover{
    border-bottom: #099BCA solid 3px;
    color: #099BCA !important;
    font-weight: 600;
}
/* .nav-item:hover{
    background-color: #099BCA;
} */
a:link{
    text-decoration: none;
}

.navbar{
    padding: 0px;
}
.bg-light{
    background-color: #F7F8F9 !important;
    /* background-color: cornflowerblue !important; */
}
.navbar-toggler{
    border: none;
    
}



@media only screen and (max-width: 600px) {
    .navBarLogo{
        font-size: 18px;
        margin-left: 3vw;
        padding: 15px 0;
    }
    .nav-link{
        font-size: 16px;
        width: 100vw;
        
    }
    .navbar-nav{
        border-top: solid rgb(98,99,101, .7);
        border-width: 1px;
    }
    #navService, #navHome, #navAbout, #navContact{
        padding: 20px 25px;
    }
    /* .navbar-toggler{
        margin-top: 10px;
        margin-bottom: 0px;
        padding-bottom: 12px;
    } */
    .mt-2, .my-2 {
        margin-top: 0px !important;
    }
    
    
  }

  @media only screen and (min-width: 601px)and (max-width: 1000px) {
    .navBarLogo{
        font-size: 24px;
    }
    .nav-link{
        font-size: 16px;
        /* width: 100vw; */
        text-align: center;
        margin: 0px 3px;
        padding: 30px;
    }
    .mb-2, .my-2 {
        margin-bottom: 0px!important;
    }
  }

.homeTop{
